.btn {
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  max-width: 100%;
  padding: 0.8rem 1.2rem;
  background-color: #3B8E65;
  font-family: "M PLUS Rounded 1c", sans-serif;
  font-weight: 700;
  font-size: 1.6rem;
  line-height: 100%;
  letter-spacing: 0.2em;
  color: #FFFFFF;
  text-align: center;
  text-decoration: none;
  outline: none;
  -webkit-transition: all 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  transition: all 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  border-radius: 2.5rem;
  cursor: pointer;
  z-index: 0;
}

@media only screen and (max-width: 768px) {
  .btn {
    padding: 1.3rem 6.0rem 1.3rem 3.2rem;
    font-size: 1.6rem;
    line-height: 2.4rem;
    letter-spacing: 0.1em;
    border-radius: 2.5rem;
  }
}

.btn span {
  position: relative;
  color: inherit;
  font-weight: inherit;
  text-decoration: none;
  z-index: 3;
}

.btn::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: block;
  border-radius: 2.5rem;
  border: 2px solid #3B8E65;
  z-index: 2;
}

@media only screen and (max-width: 768px) {
  .btn::after {
    border-width: 2px;
  }
}

.btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  background: #FFFFFF;
  width: 100%;
  height: 100%;
  -webkit-transition: -webkit-transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  transition: -webkit-transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1), -webkit-transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  -webkit-transform-origin: right top;
  transform-origin: right top;
  -webkit-transform: scale(0, 1);
  transform: scale(0, 1);
  z-index: 1;
}

.btn:hover {
  color: #3B8E65;
}

.btn:hover::before {
  -webkit-transform-origin: left top;
  transform-origin: left top;
  -webkit-transform: scale(1, 1);
  transform: scale(1, 1);
}

